<template>
    <section class="min-h-full w-full pb-[5rem] lg:pb-[2rem]">
        <div class="mb-8 flex justify-between items-center">
            <h3 class="title-vista text-2xl font-semibold text-neutral-600">Administrar cuentas</h3>
            <button @click="openModal"
                class="py-2 px-3 rounded-xl bg-orange-400 text-white text-sm hover:bg-orange-500 transition duration-300">
                Crear cuenta
            </button>
        </div>
        <div class="text-sm overflow-x-auto shadow-md sm:rounded-2xl">
            <TableUsers :listaUsuarios="ListUsers" @eliminar-usuario="eliminarUsuario" />
        </div>
        <!-- Modal -->
        <transition name="modal" mode="out-in">
            <ModalCreate v-show="openModalV" @close-modal="closeModal" @save-datos="crearCuenta" />
        </transition>
        <transition name="modal" mode="out-in">
            <ModalDelete v-if="confirmDelete" @aceptar-eliminar="aceptarEliminar" @cancelar-eliminar="cancelarEliminar" pregunta="¿Estás seguro de eliminar este usuario?"/>
        </transition>
    </section>
</template>
  
<script>
import { ref, onMounted } from 'vue';
import store from '@/store';
// import { listarUsuarios, crearUsuarios, guardarImagenLogo, eliminarUsuarioCuenta } from '@/services/AdminUsers';
import { listarUsuarios,crearUsuarios, guardarImagenLogo,eliminarUsuarioCuenta } from '@/services/AdminUsers';
import TableUsers from '@/components/TableUsers.vue';
import ModalCreate from '@/components/cuentas/ModalCreate.vue';
import ModalDelete from '@/components/generales/ModalDelete.vue';

export default {
    name: 'AdminCuentas',
    components: { TableUsers, ModalCreate,ModalDelete },
    setup() {
        const ListUsers = ref([]);
        const openModalV = ref(false);
        const confirmDelete = ref(false);
        const idUsuarioSet = ref("");
        const codempresaSet = ref("");
        onMounted(() => {
            listarUsuarios(store.state.codempresa, store.state.tipousuario)
                .then(response => {
                    ListUsers.value = response.data.usuarios;
                });
        });
        const openModal = () => {
            openModalV.value = true;
        };
        const closeModal = () => {
            openModalV.value = false;
        };
        const crearCuenta = (dataformulario) => {
            const usuario = dataformulario.usuario;
            const empresa = dataformulario.empresa;
            const password = dataformulario.password;
            const logoImage = dataformulario.logoImage;
            crearUsuarios(store.state.codempresa, store.state.tipousuario, usuario, empresa, password)
                .then(response => {
                    if (store.state.codempresa == "C000" && store.state.tipousuario == "1") {
                        console.log(response.data.codempresa)
                        guardarImagenLogo(logoImage, response.data.codempresa)
                    }
                    openModalV.value = false;
                    window.location.reload();
                });
        };
        const eliminarUsuario = (idUsuario, codempresa) => {
            idUsuarioSet.value = idUsuario
            codempresaSet.value = codempresa
            confirmDelete.value = true;
        }
        const aceptarEliminar = () =>{
            eliminarUsuarioCuenta(idUsuarioSet.value, codempresaSet.value, store.state.tipousuario)
                .then(response => {
                    console.log(response);
                    window.location.reload();
                    confirmDelete.value = false;
                });
        }
        const cancelarEliminar = () =>{
            confirmDelete.value = false;
            idUsuarioSet.value = ""
            codempresaSet.value = ""
        }
        return { ListUsers, openModalV, openModal, closeModal, crearCuenta,confirmDelete,eliminarUsuario,aceptarEliminar,cancelarEliminar };
    },
};
</script>
<style scoped>
.modal-enter-active,
.modal-leave-active {
  transition: opacity 0.3s;
}

.modal-enter-from,
.modal-leave-to {
  opacity: 0;
}

</style>


