<template>
    <label class="py-1">Usuario</label>
    <input type="text" name="usuario" :value="value" @input="$emit('update-value', $event.target.value)"
        class="w-full text-sm p-2 px-3 rounded-lg focus:outline-none focus:border-blue-500"
        placeholder="Usuario de cuenta" />
</template>
  
<script>
import { defineComponent } from "vue"

export default defineComponent({
    name: 'UserField',
    props: {
        value: {
            type: String,
            required: true
        }
    }
})

</script>
  