<template>
    <div>
      <label class="block text-sm font-medium" for="file_input">Cargar logo</label>
      <div class="flex items-center justify-center w-full">
        <label for="dropzone-file" class="flex flex-col items-center justify-center w-full border-2 text-gray-400 border-dashed rounded-lg cursor-pointer hover:bg-gray-100 bg-white border-gray-300 hover:border-gray-400 ">
          <div class="flex flex-col items-center justify-center pt-3 pb-4">
            <svg aria-hidden="true" class="w-7 h-7 mb-1 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M7 16a4 4 0 01-.88-7.903A5 5 0 1115.9 6L16 6a5 5 0 011 9.9M15 13l-3-3m0 0l-3 3m3-3v12"></path>
            </svg>
            <p class="mb-1 text-xs">Dale click para cargar imagen</p>
          </div>
          <input id="dropzone-file" type="file" class="hidden" accept="image/png" @change="handleFileSelect" />
        </label>
      </div>
      <p class="text-sm text-green-700" v-if="nombreArchivo !== ''"><i class="fa-sharp fa-solid fa-circle-check"></i> Imagen registrada: {{ nombreArchivo }}</p>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        nombreArchivo: ''
      }
    },
    methods: {
      handleFileSelect(event) {
        const file = event.target.files[0];
        this.nombreArchivo = file ? file.name : '';
        this.$emit('file-selected', file);
      }
    }
  }
  </script>
  