<template>
    <div class="fixed inset-0 flex items-center justify-center z-50">
        <div class="absolute inset-0 bg-black opacity-30 pointer-events-none"></div>
        <!-- Container modal -->
        <div class="relative z-10 p-4 bg-slate-200 rounded-xl min-w-[350px] max-w-[450px]">
            <form class="p-2 text-gray-700 w-full h-full text-sm">
                <h3 class="text-center font-bold text-lg">Crear cuenta</h3>
                <UserField @update-value="updateusuario" />
                <BusinnesField @update-value="updatebusinnes" />
                <LogoimgField @file-selected="updateImg" />
                <PasswordFieldVue @update-paswoord="updatepassword" texto="Contraseña" placeholder="Ingrese contraseña" />
                <PasswordFieldVue @update-paswoord="updatepasswordRepeat" texto="Confirmar contraseña"
                    placeholder="Repita su contraseña" />
                <p :class="{ 'text-green-500 text-xs': msgValidacion === 'Las contraseñas coinciden', 'text-red-400 text-xs': msgValidacion === 'Las contraseñas no coinciden' }"
                    v-if="msgValidacion !== ''">{{ msgValidacion }}</p>
            </form>
            <div class="btns flex gap-2 justify-center items-center pt-2">
                <button @click="closeModal"
                    class="bg-gray-400 hover:bg-gray-500 text-white text-sm p-2 px-4 rounded-xl transition duration-300">Cerrar</button>
                <button @click="saveDatos"
                    class="bg-blue-400 hover:bg-blue-500 text-white text-sm p-2 px-4 rounded-xl transition duration-300">Crear</button>
            </div>
        </div>
        <!-- Container modal -->
    </div>
</template>
  
<script>
import UserField from '@/components/cuentas/UserField.vue';
import BusinnesField from '@/components/cuentas/BusinnesField.vue';
import PasswordFieldVue from '@/components/generales/PasswordField.vue';
import LogoimgField from '@/components/cuentas/LogoimgField.vue';
export default {
    name: 'ModalCreate',
    components: { UserField, BusinnesField, PasswordFieldVue, LogoimgField },
    data() {
        return {
            // Variable para almacenar la imagen seleccionada
            logoImage: null,
            usuario: '',
            empresa: '',
            password: '',
            repeatpassword: '',
            msgValidacion: '',
            nombreArchivo: ''
        };
    },
    methods: {
        updateusuario(value) {
            this.usuario = value;
        },
        updatebusinnes(value) {
            this.empresa = value;
        },
        updatepassword(value) {
            this.password = value;
        },
        updatepasswordRepeat(value) {
            this.repeatpassword = value;
            this.validatePassword();
        },
        updateImg(file) {
            this.logoImage = file
        },
        closeModal() {
            // Emitir evento al componente padre para cerrar el modal
            this.$emit('close-modal');
        },
        saveDatos() {
            this.$emit('save-datos', {
                /* datos del formulario */
                usuario: this.usuario,
                empresa: this.empresa,
                password: this.password,
                logoImage: this.logoImage
            });
        },
        validatePassword() {
            if (this.password === this.repeatpassword) {
                this.msgValidacion = 'Las contraseñas coinciden';
            } else {
                this.msgValidacion = 'Las contraseñas no coinciden';
            }
        }
    },
};
</script>

