import axios from 'axios';

const baseURL = process.env.VUE_APP_BASE_URL;

export function listarUsuarios(codempresa,tipousuario) {
    return axios.get(`${baseURL}usuarios/listar_clientes?tipousuario=${tipousuario}&codempresa=${codempresa}`);
}

export function crearUsuarios(codempresa, tipousuario, usuario, empresa, password){
    return axios.post(`${baseURL}usuarios/crear_usuarios`,
    {
        codempresa_cuenta: codempresa,
        tipousuario: tipousuario,
        usuario: usuario,
        password: password,
        empresa_conductor: empresa
    });
}

export function guardarImagenLogo(logoImage, codempresa) {
    const formData = new FormData();
    console.log("enviara la imagen mediante http")
    formData.append('logoImage', logoImage);
    formData.append('codempresa', codempresa);
    console.log(formData)
    return axios.post(`${baseURL}usuarios/guardarLogo`, formData, {
        headers: {
            'Content-Type' : 'multipart/form-data'
        }
    });
}

export function eliminarUsuarioCuenta(idUsuario, codempresa, tipousuario){
    return axios.delete(`${baseURL}usuarios/cuenta_eliminar`, 
    {
        data: {
            idUsuario: idUsuario,
            codempresa: codempresa,
            tipousuario: tipousuario
        }
    });
}